import React from 'react';
import { Good } from './types/Good';

type Props = {
  goods: Good[] | null;
};

export const GoodsList: React.FC<Props> = ({ goods }) => (
  <ul>
    {
      goods
      && goods.map(good => (
        <li
          key={good.id}
          data-cy="good"
          style={{ color: `${good.color}` }}
        >
          {good.name}
        </li>
      ))
    }
  </ul>
);
